	module.exports = {"data":{"challengeNode":{"title":"Write a Counter with Redux","description":["Now you've learned all the core principles of Redux! You've seen how to create actions and action creators, create a Redux store, dispatch your actions against the store, and design state updates with pure reducers. You've even seen how to manage complex state with reducer composition and handle asynchronous actions. These examples are simplistic, but these concepts are the core principles of Redux. If you understand them well, you're ready to start building your own Redux app. The next challenges cover some of the details regarding <code>state</code> immutability, but first, here's a review of everything you've learned so far.","<hr>","In this lesson, you'll implement a simple counter with Redux from scratch. The basics are provided in the code editor, but you'll have to fill in the details! Use the names that are provided and define <code>incAction</code> and <code>decAction</code> action creators, the <code>counterReducer()</code>, <code>INCREMENT</code> and <code>DECREMENT</code> action types, and finally the Redux <code>store</code>. Once you're finished you should be able to dispatch <code>INCREMENT</code> or <code>DECREMENT</code> actions to increment or decrement the state held in the <code>store</code>. Good luck building your first Redux app!"],"challengeType":6,"fields":{"slug":"/front-end-libraries/redux/write-a-counter-with-redux","blockName":"Redux","tests":[{"text":"The action creator <code>incAction</code> should return an action object with <code>type</code> equal to the value of <code>INCREMENT</code>","testString":"assert(incAction().type ===INCREMENT, 'The action creator <code>incAction</code> should return an action object with <code>type</code> equal to the value of <code>INCREMENT</code>');"},{"text":"The action creator <code>decAction</code> should return an action object with <code>type</code> equal to the value of <code>DECREMENT</code>","testString":"assert(decAction().type === DECREMENT, 'The action creator <code>decAction</code> should return an action object with <code>type</code> equal to the value of <code>DECREMENT</code>');"},{"text":"The Redux store should initialize with a <code>state</code> of 0.","testString":"assert(store.getState() === 0, 'The Redux store should initialize with a <code>state</code> of 0.');"},{"text":"Dispatching <code>incAction</code> on the Redux store should increment the <code>state</code> by 1.","testString":"assert((function() { const initialState = store.getState(); store.dispatch(incAction()); const incState = store.getState(); return initialState + 1 === incState })(), 'Dispatching <code>incAction</code> on the Redux store should increment the <code>state</code> by 1.');"},{"text":"Dispatching <code>decAction</code> on the Redux store should decrement the <code>state</code> by 1.","testString":"assert((function() { const initialState = store.getState(); store.dispatch(decAction()); const decState = store.getState(); return initialState - 1 === decState })(), 'Dispatching <code>decAction</code> on the Redux store should decrement the <code>state</code> by 1.');"},{"text":"<code>counterReducer</code> should be a function","testString":"assert(typeof counterReducer === 'function', '<code>counterReducer</code> should be a function');"}]},"required":[{"link":null,"raw":null,"src":"https://cdnjs.cloudflare.com/ajax/libs/redux/3.7.2/redux.min.js"},{"link":null,"raw":null,"src":"https://cdnjs.cloudflare.com/ajax/libs/redux-thunk/2.2.0/redux-thunk.min.js"}],"files":{"indexhtml":null,"indexjs":null,"indexjsx":{"key":"indexjsx","ext":"jsx","name":"index","contents":"const INCREMENT = null; // define a constant for increment action types\nconst DECREMENT = null; // define a constant for decrement action types\n\nconst counterReducer = null; // define the counter reducer which will increment or decrement the state based on the action it receives\n\nconst incAction = null; // define an action creator for incrementing\n\nconst decAction = null; // define an action creator for decrementing\n\nconst store = null; // define the Redux store here, passing in your reducers","head":"","tail":""}}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":null,"raw":null,"src":"https://cdnjs.cloudflare.com/ajax/libs/redux/3.7.2/redux.min.js"},{"link":null,"raw":null,"src":"https://cdnjs.cloudflare.com/ajax/libs/redux-thunk/2.2.0/redux-thunk.min.js"}],"nextChallengePath":"/front-end-libraries/redux/never-mutate-state","id":"5a24c314108439a4d4036157"},"slug":"/front-end-libraries/redux/write-a-counter-with-redux"}}

